<template>
  <div class="google-map" :id="mapName"></div>
</template>

<script>
import gmapsInit from "../utils/gmaps";

export default {
  name: "bird-map",
  props: ["name", "selectedBird", "birdData", "countyData", "selectedYear"],
  watch: {
    selectedBird() {
      this.setMapStyles();
    },
    selectedYear() {
      this.setMapStyles();
    }
  },
  data() {
    return {
      vueGMap: null,
      mapName: this.name + "-map",
      API_KEY: "AIzaSyDfg24RRLOVbQFWVCN0Mx4bMGQqmpKqPfs",
      minZoomLevel: 5,
      maxZoomLevel: 10,
      UKBounds: {
        north: 61.434329,
        south: 49.758414,
        east: 3.229716,
        west: -11.55975
      }
    };
  },
  methods: {
    googleMapsFailedToLoad() {
      this.vueGMap = "Error occurred";
    },
    setMapStyles() {
      this.vueGMap.data.setStyle(feature => {
        var name = feature.getProperty("name");
        var color = "#000";
        this.birdData.map(bird => {
          if (bird.species === this.selectedBird && bird.county === name) {
            feature.setProperty("bird", bird);
            color = this.setColor(bird[this.selectedYear]);
          }
        });
        return {
          fillColor: color,
          fillOpacity: 0.8,
          strokeColor: "#000",
          strokeOpacity: 0.2,
          strokeWeight: 1
        };
      });
      this.vueGMap.data.addListener("mouseover", event => {
        var county = event.feature.getProperty("name");
        this.vueGMap.data.revertStyle();
        this.vueGMap.data.overrideStyle(event.feature, { fillOpacity: 1.0 });
        this.requestHighlight(county);
      });
      this.vueGMap.data.addListener("mouseout", () => {
        this.vueGMap.data.revertStyle();
        this.requestReset();
      });
    },
    requestHighlight(county) {
      this.$emit("highlight", county);
    },
    requestReset() {
      this.$emit("reset");
    },
    setColor(percent) {
      if (percent > 95) {
        return "#990100";
      } else if (percent > 90) {
        return "#cc0001";
      } else if (percent > 85) {
        return "#fe0000";
      } else if (percent > 80) {
        return "#ff3300";
      } else if (percent > 75) {
        return "#ff6600";
      } else if (percent > 70) {
        return "#fe9900";
      } else if (percent > 65) {
        return "#ffcc00";
      } else if (percent > 60) {
        return "#fffc00";
      } else if (percent > 55) {
        return "#cbfe33";
      } else if (percent > 50) {
        return "#98ff66";
      } else if (percent > 45) {
        return "#67ff9a";
      } else if (percent > 40) {
        return "#33ffcc";
      } else if (percent > 35) {
        return "#00ffff";
      } else if (percent > 30) {
        return "#00ccff";
      } else if (percent > 25) {
        return "#0099ff";
      } else if (percent > 20) {
        return "#0166ff";
      } else if (percent > 15) {
        return "#0000fe";
      } else if (percent > 10) {
        return "#0033ff";
      } else if (percent > 5) {
        return "#0000cc";
      } else {
        return "#000098";
      }
    },
    requestLoaded() {
      this.mapLoaded = true;
      this.$emit("mapLoaded");
    },
    requestLoading() {
      this.$emit("mapLoading");
    }
  },
  async mounted() {
    try {
      const google = await gmapsInit();
      this.requestLoading();
      const element = this.$el;
      const options = {
        zoom: 6,
        center: { lat: 56.0053834, lng: -2.5742888 },
        restriction: {
          latLngBounds: this.UKBounds,
          strictBounds: false
        },
        disableDefaultUI: true,
        zoomControl: true,
        minZoom: this.minZoomLevel,
        maxZoom: this.maxZoomLevel,
        scaleControl: true,
        styles: [
          {
            elementType: "geometry",
            stylers: [{ color: "#242f3e" }]
          },
          {
            elementType: "labels.text.stroke",
            stylers: [{ visibility: "off" }]
          },
          {
            elementType: "labels.text.fill",
            stylers: [{ color: "#746855" }]
          },
          {
            featureType: "administrative",
            elementType: "labels",
            stylers: [{ visibility: "off" }]
          },
          {
            featureType: "poi",
            elementType: "labels",
            stylers: [{ visibility: "off" }]
          },
          {
            featureType: "road",
            elementType: "geometry",
            stylers: [{ visibility: "off" }]
          },
          {
            featureType: "road",
            elementType: "geometry.stroke",
            stylers: [{ visibility: "off" }]
          },
          {
            featureType: "road",
            elementType: "labels",
            stylers: [{ visibility: "off" }]
          },
          {
            featureType: "road.highway",
            elementType: "geometry.stroke",
            stylers: [{ visibility: "off" }]
          },
          {
            featureType: "road.highway",
            elementType: "labels.text.fill",
            stylers: [{ visibility: "off" }]
          },
          {
            featureType: "transit",
            elementType: "geometry",
            stylers: [{ visibility: "off" }]
          },
          {
            featureType: "transit.station",
            elementType: "labels.text.fill",
            stylers: [{ visibility: "off" }]
          },
          {
            featureType: "water",
            elementType: "geometry",
            stylers: [{ color: "#17263c" }]
          },
          {
            featureType: "water",
            elementType: "labels.text.fill",
            stylers: [{ visibility: "off" }]
          },
          {
            featureType: "water",
            elementType: "labels.text.stroke",
            stylers: [{ visibility: "off" }]
          }
        ]
      };
      this.vueGMap = new google.maps.Map(element, options);
      this.vueGMap.data.addGeoJson(this.countyData);
      this.vueGMap.fitBounds(this.UKBounds);
      this.setMapStyles();
      this.requestLoaded();
    } catch (error) {
      this.googleMapsFailedToLoad;
    }
  }
};
</script>

<style lang="scss">
#bird-map-map {
  height: 100vh;
  width: 100vw;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
  margin: 0 auto;
  background: gray;
}
</style>
